	module.exports = {"data":{"markdownRemark":{"html":"<h1>tmux基本操作</h1>\n<p>电脑换回了win10系统，使用bash on windows进行开发，有个问题一直很困扰。\n在windows系统下面，少见非常优秀的，类似iTerm2那样的终端软件。使用cmder对于bash的美化确实起到了很大的作用，但是对屏幕的管理依然不方便。最近留意到了tmux这款终端复用工具，简单使用后，发现它的确非常棒，给我带来了不少的便利。</p>\n<h2>Session</h2>\n<p>创建一个Session，默认情况下，当在shell中输入<code>tmux</code>时，就会自动创建一个名称为<code>0</code>的Session</p>\n<pre><code class=\"language-bash\">#　创建Session\ntmux\n# 查看当前建立的Session，使用下面两个命令\ntmux list-session\n# 缩写\ntmux ls\n0: 1 windows (created Sat Jan 21 11:27:33 2017) [55x35]\n# 创建一个指定名称的Session\ntmux new -s $name\n# 使用t参数关闭指定的Session\ntmux kill-session -t $name\n# 将指定的Session放置到后台中\ntmux detach -t  $name\n# 加载处于在后台中的Session\ntmux attach-session -t $name\n# 完全退出，关闭所有的Session\ntmux kill-server\n</code></pre>\n<h2>Window</h2>\n<p>在Session之下，可以开设很多得当Window，其实当我们创建一个Session时，就已经会默认创建一个Window了。</p>\n<p>在Window中，可以使用很多命令进行闪转腾挪了，默认调用命令的快捷键是<code>Ctrl-b</code>，看下面的例子：</p>\n<pre><code class=\"language-bash\">#Ctrl-b &#x3C;command>\n# 列出当前所有的Window\nCtrl-b w \n(0)  0: ~  \"DESKTOP-IE8ULOJ\"\n(1)  1: ~  \"DESKTOP-IE8ULOJ\"\n(2)  2: ~- \"DESKTOP-IE8ULOJ\"\n(3)  3: ~* \"DESKTOP-IE8ULOJ\"\n# 创建一个Wind ow （create）\nCtrl-b c\n# 切换到下一个Window （next）\nCtrl-b n\n# 切换到上一个Window（previous）\nCtrl-b p\n# 切换到指定序号的Window\nCtrl-b [0-9]\n# 重命名当前窗口的名称\nCtrl-b ，\n# 关闭当前窗口\nCtrl-b &#x26;\n</code></pre>\n<h2>Pane</h2>\n<p>在Window层级的切换时全屏的，可以继续分，往下的这个层级叫做Pane</p>\n<pre><code class=\"language-bash\"># 垂直分割\nCtrl-b %\n# 水平分割\nCtrl-b “”\n# 在窗口之间切换\nCtrl-b o\n# 按照指定的方向切换\nCtrl-b 方向键\n# tmux默认有几种Pane的布局方式，这个命令用来在默认的布局方式中切换\nCtrl-b 空格\n# 关闭当前Pane\nCtrl-b x\n</code></pre>","frontmatter":{"title":"tmux日常操作","date":"2017-01-22","tags":["Tmux","Linux"]}}},"pathContext":{"slug":"/2017-01-22---tmux-operation/"}}

